<template>
	<div class="personal-content-body">
		<div class="content">
			<!-- <div class="content-title clear">
				<div class="left">出勤记录</div>
			</div> -->
			<!--<el-breadcrumb separator-class="el-icon-arrow-right navBar">
				<el-breadcrumb-item :to="{name:'administrativeManageHomepageNew'}">行政管理</el-breadcrumb-item>
				<el-breadcrumb-item>出勤记录</el-breadcrumb-item>
			</el-breadcrumb>-->
			<div class="credit-box credit-box1">
				<span>展示列：</span>
				<div class="credit_right">
					<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
						<el-checkbox v-for="(item,index) of cities" :key="index" :label="item.paramsKey">{{item.paramsValue}}</el-checkbox>
					</el-checkbox-group>
				</div>
			</div>
			<div class="credit-box">
				<span>时间：</span>
				<el-date-picker v-model="BeginDate" type="date" :editable='false' placeholder="开始日期" value-format="yyyy-MM-dd" :clearable="false" @change='toGetDataList' class="credit-box-data-select  credit-box-data-select1">
				</el-date-picker>
				<span>至</span>
				<el-date-picker v-model="LastDate" type="date" :editable='false' placeholder="结束日期" value-format="yyyy-MM-dd" :clearable="false" @change='toGetDataList' class="credit-box-data-select credit-box-data-select2">
				</el-date-picker>
				<span>部门/人员：</span>
				<el-select v-model="depart_id" placeholder="请选择" class="select-department" :clearable="true" @change="filterData()">
					<el-option v-for="(item,index) of userList" :key="index" :label="item.name" :value="item.id">
					</el-option>
				</el-select>
				<el-checkbox v-model="is_lizhi" @change="filterData()">离职员工<span class="tishi">（保留三个月）</span></el-checkbox>
			</div>
			<div class="line"></div>
			<div class="daochu_btn">
				<div class="right" @click="todownLoadExcel">
					<div class="table-group-title-btn btn-blue">
						导出报表
					</div>
				</div>
				<div class="left">
					<div class="intro_btn" @click="showIntroModal"><i class="el-icon-question"></i>说明</div>
				</div>
			</div>
			<div class="table" style="padding: 0;">
				<el-table :data="haveFilterData" border style="width: 100%">
					<el-table-column v-for="(item,index) of headData" :index="index" :key="index" :prop="item.titleKey" :label="item.titleValue" fit min-width='150px' v-if="checkedCities.indexOf(item.type)!=-1||item.type==0">
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
			
		</div>
		<!-- 新建/编辑文件夹弹窗 -->
		<el-dialog title="报表字段说明" :visible.sync="introDialogVisible" width="400px" center :append-to-body='true' class="dialog-content-movefile applyfun-modal">
			<div class="dialog_content" style="padding:0px 20px 20px">
				<div>
					<strong class="">出勤天数：</strong>统计本月法定工作日天数
				</div>
				<div>
					<strong class="">出差天数：</strong>出差天数会自动减去假期，如周五至下周一出差，此处只记录为2天
				</div>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import {
		getUserCompany,
		attendanceList,
		attendanceExcel,
	} from "../../config/api";
	import {
		dialogConfirm,
		dialogConfirmHasbtn
	} from "../../config/mUtils";
	export default {
		data() {
			return {
				introDialogVisible: false,
				checkedCities: [
					"is_chuqin",
					"is_xiuxi",
					"is_chucai",
					"is_waichu",
					"is_qinjia"
				],
				cities: [{
						paramsKey: "is_chuqin",
						paramsValue: "出勤天数"
					},
					{
						paramsKey: "is_xiuxi",
						paramsValue: "休息时间"
					},
					{
						paramsKey: "is_chucai",
						paramsValue: "出差时间"
					},
					{
						paramsKey: "is_waichu",
						paramsValue: "外出时间"
					},
					{
						paramsKey: "is_qinjia",
						paramsValue: "请假时间"
					}
				],
				BeginDate: "",
				LastDate: "",
				depart_id: "",
				is_lizhi: false,

				userList: [],
				allData: [],
				headData: [],
				haveFilterData: []
			};
		},
		mounted() {},
		created() {
			this.toGetUserCompany();
			let LastDate = new Date().Format("yyyy-MM-dd");
			let BeginDate = new Date().Format("yyyy-MM-dd").split("-");
			BeginDate.splice(2, 1, "01");
			BeginDate = BeginDate.join("-");
			this.BeginDate = BeginDate;
			this.LastDate = LastDate;
			this.toGetDataList();
		},
		methods: {
			showIntroModal() {
				this.introDialogVisible = true;
			},
			//获取数据
			handleCheckedCitiesChange(value) {},
			toGetUserCompany() {
				getUserCompany().then(res => {
					if(res.data.msgNo == 1) {
						this.userList = res.data.payload;

					}
				});
			},
			filterData() {
				this.haveFilterData = [];
				let departName = "";
				this.userList.forEach(element => {
					if(element.id == this.depart_id) {
						departName = element.name;
					}
				});
				if(departName != "") {
					this.allData.forEach(element => {
						if(element.is_depart == departName) {
							this.haveFilterData.push(element);
						}
					});
				} else {
					this.haveFilterData = JSON.parse(JSON.stringify(this.allData));
				}
				if(!this.is_lizhi) {
					this.haveFilterData.forEach((item, index) => {
						if(item.status == "离职") {
							this.haveFilterData.splice(index, 1);
						}
					});
				}
				// console.log(this.haveFilterData);
			},
			//出勤列表
			toGetDataList(type) {
				let params = {
					is_chuqin: 1,
					is_xiuxi: 1,
					is_chucai: 1,
					is_waichu: 1,
					is_qinjia: 1,
					is_lizhi: 1,
					is_depart: 1,
					depart_id: ""
				};

				if(this.BeginDate == "" || this.LastDate == "") {
					dialogConfirm("info", "请选择时间");
					return false;
				} else {
					let data1 = new Date(this.BeginDate);
					let data2 = new Date(this.LastDate);
					if(data1.getTime() > data2.getTime()) {
						dialogConfirm("info", "开始时间不能大于结束时间");
						return false;
					} else {
						params.BeginDate = this.BeginDate;
						params.LastDate = this.LastDate;
					}
				}
				attendanceList(params).then(res => {
					if(res.data.msgNo == 1) {
						this.allData = res.data.payload.data;
						let headData = res.data.payload.head;

						this.headData = [];
						for(let i in headData) {
							let newObj = {
								titleKey: i,
								titleValue: headData[i],
							}
							if(i == 'shijia' || i == 'chanjia' || i == 'nianjia' || i == 'bingjia' || i == 'peichanjia' || i == 'sangjia' || i == 'tiaojia') {
								newObj.type = 'is_qinjia'
							} else if(i == 'user_name' || i == 'status' || i == 'is_depart') {
								newObj.type = 0;
							} else {
								newObj.type = i;
							}
							this.headData.push(newObj);
						}
						this.filterData();
					}
				});
			},
			//导出出勤报表
			todownLoadExcel() {
				let params = {};
				for(let i of this.checkedCities) {
					params[i] = 1;
				}
				params.BeginDate = this.BeginDate;
				params.LastDate = this.LastDate;
				params.is_depart = 1;
				params.depart_id = this.depart_id
				if(this.is_lizhi) {
					params.is_lizhi = 1
				}
				console.log(params);
				attendanceExcel(params).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload;
					} else {
						dialogConfirm('info', res.data.message)
					}
				});
			}
		}
	};
</script>
<style lang="scss" scoped>
	.table{
		padding: 0 !important;
	}
	.content {
		width: 100%;
		min-height: 100%;
		// min-width: 1000px;
		background-color: white;
		padding: 20px 30px;
	}
	
	.content-title {
		border-bottom: 1px solid #f1f1f1;
		margin-bottom: 15px;
	}
	
	.credit-box {
		display: flex;
		align-items: baseline;
		margin-bottom: 16px;
		&>span {
			font-size: 14px;
			color: #35383a;
		}
		&>span:nth-child(1) {
			margin-right: 20px;
		}
		.credit_right {
			width: calc(100% - 200px);
		}
	}
	
	.credit-box1 {
		margin-top: 10px;
	}
	
	.credit-box .el-checkbox {
		margin-right: 30px;
		margin-left: 0px;
		margin-bottom: 5px;
	}
	
	.credit-box .el-checkbox__label {
		color: #35383a !important;
	}
	
	.credit-box-data-select1 {
		margin-right: 8px;
		border-radius: 15px;
	}
	
	.credit-box-data-select2 {
		border-radius: 15px;

		margin-left: 8px;
		margin-right: 100px;
	}
	
	.select-department {
		margin-right: 18px;
	}
	
	.tishi {
		color: #a0a4aa;
	}
	
	.line {
		widows: 100%;
		border-bottom: 1px solid #f1f1f1;
		margin: 20px 0px;
	}
	
	.daochu_btn {
		width: 100%;
		height: 30px;
		margin-bottom: 20px;
	}
	
	.intro_btn {
		height: 30px;
		font-size: 14px;
		color: #3C98E8;
		cursor: pointer;
		i {
			color: #3C98E8;
			margin-right: 2px;
		}
	} // .gutter{
	//     border-bottom: none !important; 
	//     border-bottom-width: 0px !important;
	// }
</style>


